//import "babel-polyfill";
import QuizMC from "./quizmc";

export default class App {
    constructor() {}

    startUp() {
        console.log('### APP: startUp: ');
        function getJsonFileName(loc) {
            let [fileName, foldername, ...rest] = loc.href.split('/').reverse();
            let jsonFile = loc.origin + '/' + foldername + '/' + fileName.split('.')[0] + '.json';
            console.log('APP: jsonFile: ', jsonFile);
            return jsonFile;
        }
        function validateResponse(response) {
            console.log('APP: validateResponse: ', response);
            if (!response.ok) {
                throw Error(response.statusText);
            }
            return response;
        }
        function readResponseAsJSON(response) {
            console.log('APP: readResponseAsJSON: ', response);
            return response.json();
        }
        function logResult(result) {
            console.log('APP: logResult: ', result);
            return result;
        }
        function logError(error) {
            console.log('Looks like there was a problem: \n', error);

        }
        fetch(getJsonFileName(window.location))
            .then(validateResponse)
            .then(readResponseAsJSON)
            .then(logResult)
            .then(res => this.startUpQuiz(res))
            .catch(err => {
                logError(err);
                this.startUpDemoQuiz();
            });
    }

    startUpQuiz(json) {
        console.log('APP: startUpQuiz: ', json);
        this.quiz = new QuizMC(json);
        this.quiz.startUp();
        return Promise.resolve(json);
    }

    startUpDemoQuiz() {
        let demo = {
            "title": "Title quiz_clickText",
            "id": "q456",
            "type": "quiz_clickText_mc",
            "multipleAnswers": false,
            "maxScore": 2,
            "questionArray": [{
                description: "this is q1 text",
                id: 0,
                qNum: 1,
                ans: [2, 4],
                userAnswer: [2]
            }]
        };
        console.log('APP: startUpDemoQuiz: ', demo);
        this.quiz = new QuizMC(demo);
        this.quiz.startUp();
    }


}

